import { mapState } from 'vuex';
export default{
	data(){
		return{
			//是否显示返回按钮



						
		}
	},
	onLoad(){

		//小程序隐藏返回按钮
		this.showBack = false;

	},
	computed: {
		...mapState(['hasLogin' , 'userInfo'])
	},
	methods:{
		//返回上一页
		back() {
			uni.navigateBack();
		},
		navToBuy(){//跳转购买
			if(!this.hasLogin){//如果没有登录
				uni.navigateTo({
					url: '../../../pages/public/login'
				})
				return;
			}
			uni.navigateTo({
				url: '../swop/base/toBuy'
			})
		}
	}
}
